.accordion {
	&__item {
		@apply bg-white border border-gray-200 overflow-hidden;
	}
	.accordion__item:not(:first-of-type) {
		@apply border-t-0;
	}
	&__header {
		@apply mb-0;
	}
	&__button {
		@apply relative flex items-center w-full py-4 px-5 text-base text-gray-800 text-left bg-white border-0 rounded-none focus:outline-none;
	}
	&__collapse {
		transition: max-height .3s ease-in-out;
		max-height: 0px;
		&.collapsed {
			max-height: 0px;
		}
	}
	&__body {
		@apply py-4 px-5;
	}
}